@keyframes heartbeat {
    0% {
        transform: scale(1);
    }
    4% {
        transform: scale(1.1);
    }
    7% {
        transform: scale(1);
    }
    11% {
        transform: scale(1.07);
    }
    18% {
        transform: scale(1);
    }
}
.anime-object {
    animation: heartbeat 4s infinite ease-in-out;
}
.circle {
    width: 100px;
    height: 100px;
    border-radius: 50px;
    margin: 20px auto;
    background-color: #15aaa1;
}
