
body {
  color: #000000;
  background-color: #ffffff;
  font-size: 10.5pt;
  font-family: 'メイリオ', Meiryo, sans-serif;
  line-height: 1.5em;
  letter-spacing: 1px;
  text-align: left;
  margin: 0 0 0 0;
  padding: 0 0 0 0;
}

h1, h2,h3, h4, h5, h6, p, ul, ol, li, dl, dt, dd, table, th, tr, form, button, select, input, textarea {
  font-size: 100%;
  margin: 0;
  padding: 0;
}

select, button, input, textarea, option {
  box-sizing: border-box;
  font-family: 'メイリオ', Meiryo, sans-serif;
}

div.page {
  /* margin: 10px auto 0 auto; */
  padding: 0 0 0 0;
  /* min-width: 850px; */
  width: 98%;
  height: 100%;
}



